import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: () => import('@/components/HomePage.vue')
    },
    {
      path: '/GPReID',
      name: 'GPReID',
      component: () => import('@/components/GPReID.vue')
    }
  ]
})



